.modal {
    position: fixed;
    top: 0;
    left: calc((100vw - 100%)/2); /*no jump from change overflow*/
    z-index: 1050;
    display: none;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.5);
}
.modal-dialog {
    max-width: 500px;
    margin: 50px auto;

}
.modal-content {
    position: relative;
    width: 100%;
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    max-height: 80vh;    /* 80% of visible part from window */
    overflow-y: auto;
}
.modal .close {
    position: absolute;
    top: 10px;
    right: 14px;
    font-size: 30px;
    font-weight: 700;
    border: none;
    background-color: transparent;
    cursor: pointer;
}
.modal-header, .modal-body {
    padding: 20px 15px 15px 15px;
    border-bottom: 1px solid var(--color-border);
}
.modal-title {
    font-weight: 500;
    font-size: 20px;
}
.modal-footer {
    padding: 20px 15px 15px 15px;
}